.Chat {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 90vh;
  margin: auto;
  width: 70%;
  border-radius: 10px;
}

.App-header {
  margin: 10px auto;
  padding: 10px 0;
  overflow: visible;
  width: 100%;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #6f6f6f47;
}

.Messages-list {
  padding: 20px 0;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  list-style: none;
  padding-left: 0;
  flex-grow: 1;
  overflow: auto;
}

.Messages-message {
  display: flex;
  margin-top: 10px;
}

.Messages-message.currentMember {
  /*justify-content: flex-end;*/
  flex-direction: row-reverse;
  text-align: right;
}

.Message-content {
  display: inline-block;
}

.currentMember > .Message-content {
  align-items: flex-end;
}

.Messages-message > .avatar {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  display: inline-block;
  margin: 0 10px -10px;
}
.avatar div {
  text-align: center;
  margin-top: 5px;
  color: white;
  text-transform: uppercase;
  border-bottom: 2px solid #6f6f6f7c;
  border-radius: 50%;
  height: 35px;
  width: 35px;
}

.Message-content > .username {
  display: block;
  color: gray;
  font-size: 10px;
  padding-bottom: 4px;
  padding-top: 10px;
}

.Message-content > .text {
  padding: 10px;
  max-width: 400px;
  margin: 0;
  border-radius: 5px;
  background-color: cornflowerblue;
  color: white;
  display: inline-block;
}

.currentMember > .Message-content .text {
  background-color: white;
  color: #6f6f6f;
}

form {
  display: flex;
  width: 100%;
  justify-content: space-between;
  max-width: 900px;
  margin: 0 auto 40px;
}

input {
  padding: 5px;
  font-size: 16px;

  border: 1px solid #dfe1f0;
  width: 55%;
  background: #f6f7ff;
  flex-grow: 1;
}

input button {
  padding: 5px 10px;
  font-size: 16px;
  background-color: #6f6f6f;
  color: white;
  border: none;
}
button {
  border: none;
}
button:hover {
  color: white;
}
.btn-send {
  color: white;
  background: #4a4ce5f5;
  width: 7rem;
  border-radius: 3px;
  margin-left: 10px;
  border: none !important;
}

.wrap-one {
  margin-top: 50px;
}
.wrap-one h1 {
  margin: auto;
  color: black;
  font-size: 60px !important;
  text-align: center;
}
.wrapper {
  margin: 0px 200px;
  color: black;
  margin-top: 200px;
  text-align: center;
  display: flex;
  gap: 200px;
  justify-content: center;
  text-align: justify;
}
.wrapper img {
  margin-bottom: 30px;
}
.wrapper-one,
.wrapper-two p {
  margin: auto;
}
@media only screen and (max-width: 600px) {
  .wrapper {
    flex-direction: column;
    margin: 30px 30px;
    justify-content: center;
    gap: 20px;
  }
  .App {
    height: none;
  }
  .container {
    width: 100% !important;
  }
}
.dropbtn {
  border-radius: 50%;
  width: 55px;
  height: 55px;
  text-transform: uppercase;
  background-color: #4a4ce5f5;
  border: none !important;
  color: white;
  font-size: 27px;
}

.dropdown {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  color: none;
}

.dropdown-content {
  display: none;
  position: absolute;
  margin-top: 5px;
  transform: translateX(-315px);
  border-radius: 15px;
  border: 3px solid rgba(128, 128, 128, 0.144);
  background-color: #f1f1f1;
  min-width: 330px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.318);
  z-index: 1;
}

.dropdown-content a {
  color: #6f6f6f7c;
  padding: 12px 16px;
  text-decoration: none !important;
  list-style-type: none;
  border-bottom: 2px solid #e5e8ec;
  display: block;
  text-transform: none !important;
}

.dropdown-content a:hover {
  border-radius: 10px;
  color: white;
  background-color: rgba(128, 128, 128, 0.144);
  border-bottom: none;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #3e8e41;
}

.headerbtn {
  margin: 5px 0px;
  background-color: rgba(227, 21, 21, 0.784);
  color: white;
  padding: 1px 20px;
  border-radius: 3px;
  font-size: 25px;
}
.headerbtn:hover {
  background-color: rgba(100, 10, 10, 0.912);
}
